

import axios from 'axios';
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name:'create',
data:function(){
return{
title:'',
content:'',

toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
preview: true, // 
}
}

},
components: {
XTextarea,
Group,
XButton,
mavonEditor,
axios
},
methods:{

uploadImage:function(pos,file){
var app =this;


console.log(pos);
console.log(file);

var params = new FormData();
params.append('smfile',file);

axios.post('https://sm.ms/api/upload',params,{



headers: { 'Content-Type': 'multipart/form-data' }
}).then(function(response){


app.$refs.md.$img2Url(pos,response.data.data.url);

});


},

addtopic:function(){

var app=this;

this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(response){

if(response.data&&response.data.success){

app.$router.replace('/topiclist/dev');
}
})
}
}
};



